body {
  margin: 0;
   font-size:16px;
     background:linear-gradient(90deg,#8b5912,#816338);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',"Palatino Linotype", "Book Antiqua", Palatino, serif,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}


.App-header{
   display:flex;
   justify-content: center;
   height:4em;
   background-color:blue;
   background:linear-gradient(45deg,#a40977,#9419c4);
}
.App-logo{
   height:4em;
   z-index:1;
   
}

.Tab-container{
   display:grid;
   grid-template-columns: auto auto auto;
   justify-content:center;
   text-align:center;
   background:linear-gradient(45deg,#a40977,#9419c4);
   height:3em;
   border:black solid .4em;
   z-index:2
}
.Tab-element{
   text-align:center;
   width:12.9em;
   text-align: center;
   margin-top:0em;
   border:black solid .09em;
   z-index:2
 
}
.Selected{
   z-index:2;
   width:12.9em;
   text-align:center;
   margin-top:0em;
   border:black solid .09em;
   background-color:#4d026b;
   color:white
 
}
.Tab{
   display:flex;
   flex-direction:column;
   flex-wrap:wrap;
   justify-content:center;
   align-content:center;
   text-align:center;
   
   
}
.Para{
   width:25em;
   text-justify:word-spacing;
   text-align:justify;
   text-indent:3em;
   align-self:center;
   position:relative;
   
}

.Deliver-img{
   height:20em;
   display:inherit;
   width: 35em;
   align-self:center;
}
/*Menu*/
#Menu{
  background:linear-gradient(45deg,#8b5912,#816338); 
  display:flex;
  flex-direction:column;
  flex-wrap:wrap;
  justify-content:center;
}
.Food-type{
   font-size:4em;
   font-weight:bold;
   width:6em;
   margin-left:34%;
   Color:white;
   border:solid black .2em;
   text-align:center;
}
.Menu-Items{
   display:grid;
   grid-template-columns: auto auto auto auto auto auto;
   padding:1em 0em 1em 0em;
   border:solid black 1.3em;
}
.Inside{
   height:25%;
   text-align:center;
   font-size:.7em;

}
.Show-Left{
   grid-row: 1/5; 
   font-size:2.5em;
   align-self:center;
   align-content:center;
   padding:.39em;
   width: 3.8em;
   color:white;
   height:100%;
   display:flex;
   flex-direction:column;
   border-right:solid black .4em;
}

.Show-Right{
   grid-area: 1 / 6 / 5 / 6; 
   font-size:2.5em;
   align-self:center;
   align-content:center;
   padding:.39em;
   width: 3.8em;
   background-color:blue;
   height:100%;
   word-break:break-work;
   display:flex;
   flex-direction:column;
   background-color:inherit;
   border-left:solid black .4em;
   color:white;
}
.Food-Item{
   background-color:green;
   border:black solid .4em;
   width:10em;
   height:10em;
   margin:1em;
    Color:white;
   font-weight:bold;
   font-size:1em;
   background-color:black;
}
.Food{
   height:10em;
   width:10em;
   
   
}
.Description{
      height:10em;
   width:10em;
   
  
}
/* Orders*/

.form-Space{
  display:flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  background-color:blue;
}
#add-todo{

  border-radius: 3em;
  padding:2em 2em 1em 2em;
  width: 38em;
  display:block;
  z-index: 3; 
  opacity: .9;
  color:black;
   background:linear-gradient(45deg,#a40977,#9419c4); 
}
#boton-container-f{
  display:flex;
  flex-direction:row-reverse;
  width:20em;
  justify-content: space-evenly;
  margin-left:19.5em;
}

label {
  padding: 0.75em 0.75em 0.75em 0em;
  display: inline-block;
  font-weight:bold;
  
}
.col-label-f {
  float: left;
  width: 27%;
  margin-top: 0.375em;
  text-align:left
}

.col-input-f {
  float: left;
  width:66%;
  margin-top: 0.375em;
}
.line:after {
  content: "";
  display: table;
  clear: both;
}

.boton {
   margin-top:1em;
   height:1.8em;
   width:6em;
   background-color:#4d026b;
   color:white;
   padding:.2em;
   border:black solid .005em;
}

input[type=text],select, textarea, input[type=tel],input[type=email] {
  width: 100%;
  padding: .75em;
  border: .07em solid Black;
  border-radius: 0.5625em;
  resize: vertical;
  color:Black;
  background:linear-gradient(45deg,#a40977,#9419c4)
}

/*+*/
#Order{
   display:flex;
   justify-content:center;

   background:linear-gradient(45deg,#8b5912,#816338);
}
.Order-Squares{
   display:grid;
   grid-template-columns: auto auto auto auto auto auto;
   border:solid black .15em;
   background:linear-gradient(45deg,#a40977,#9419c4); 
   text-align:center;
   font-weight:bold;
}
input[type=number]{
   
   width: 20%;
   padding: .2em;
   margin-bottom:.5em;
   background-color:#4d026b;
   color:white;
   border:white solid .005em
}
.Order-Item{
   border:solid black .15em;
   width:12em;
}
.Order-Item-Last{
   border:solid black .15em;
   width:12em;
   grid-area: 5 / 6 / 5 / 6;
}
.Order-Item-Total{
   border:solid black .15em;
   display:grid;
   align-content:left;
   justify-content:left;
   grid-template-rows: 1em 1em 1em 1em 1em 1em auto;
   grid-template-columns:auto auto auto auto auto;
   grid-area: 5 / 2 / 5 / 6;
   align-content:left;
   height:auto;
   text-align:left;
}
.Listed{
  font-size:.8em;
  text-indent:1em;
}
.Total{
   grid-area: 7 / 1 / 7 / 1;
   text-indent:.8em;
   width:12em;
   font-size:1.3em;
}
.Order-Boton1{
   position:absolute;
   margin-left:20em;
   margin-top:8.4em;
   height:1.8em;
   width:6em;
   background-color:#4d026b;
   color:white;
   padding:.2em;
   border:black solid .005em;
}
.Order-Boton2{
    position:absolute;
   margin-left:12em;
   margin-top:8.4em;
   background-color:#4d026b;
   color:white;
   height:1.8em;
   padding:.2em;
   width:6em;
   border:black solid .005em;
}
/* Receipt*/
.Receipt{
   
}
.Receipt-Holder{
   border:black .3em solid;
   width: auto;
   margin-top:4em;
   background-color:white;
   align-content:left;
   font-size:.5em
}
.Receipt-Item{
   text-align:left;
   margin-left:1em;
   margin-right:1em;
   word-break: break-word;
   width:17em;
}
.Holds-Meta{
   display:grid;
   grid-template-columns:26em 7em;
   justify-content:right;
   padding-right:3em
}
.Receipt-Meta-Item{
   text-align:left;
   
}
.Orders-Listing{
   display:grid;
   grid-template-columns:28em 7em;
}
.Contain-Total-Receipt{
   display:grid;
   grid-template-columns:23.1em 14em;
}
.Total-Receipt{
   grid-column:2/2;
}
.receipt-boton-container{
   display:flex;
   flex-direction:row-reverse;
   justify-content:center;
   
}
.receipt-boton{
   margin-top:1em;
   height:1.8em;
   width:7em;
   background-color:#4d026b;
   color:white;
   padding:.2em;
   border:black solid .005em;
   margin-left:1em;
}
.Paying{
   display:flex;
   flex-direction:column;
   justify-content:center;
   text-align:center;
   background:linear-gradient(45deg,#a40977,#9419c4); 
   
}

.boton-checkout{
   margin-top:1em;
   height:1.8em;
   width:7em;
   background-color:#4d026b;
   color:white;
   padding:.2em;
   border:black solid .005em;
   margin-left:1em;
}
.Invisible{
   display:none
}
